.error {
  color: red;
  font-weight: 700;
  display: none;
}

.loader {
  font-weight: 700;
}

.loading {
  width: 40px;
  height: 60px;
  animation: heartBeat 1.2s cubic-bezier(.215, .61, .355, 1) infinite;
  position: relative;
}

.loading:before, .loading:after {
  content: "";
  width: 40px;
  height: 60px;
  transform-origin: 50% 68%;
  background: #ff3d00;
  border-radius: 50px 50px 0 0;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(45deg);
  box-shadow: inset 5px 4px 5px rgba(0, 0, 0, .267);
}

.loading:after {
  transform: rotate(-45deg);
}

@keyframes heartBeat {
  0% {
    transform: scale(.95);
  }

  5% {
    transform: scale(1.1);
  }

  39% {
    transform: scale(.85);
  }

  45% {
    transform: scale(1);
  }

  60% {
    transform: scale(.95);
  }

  100% {
    transform: scale(.9);
  }
}

.slim-select {
  display: inline-block;
  position: relative;
}

.slim-select .ss-list {
  z-index: 9999;
  width: 100%;
  max-height: 200px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: absolute;
  overflow-y: auto;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.slim-select .ss-list .ss-option {
  cursor: pointer;
  padding: 8px 12px;
}

.slim-select .ss-single-selected {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px 12px;
  display: block;
}

.slim-select .ss-arrow {
  pointer-events: none;
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translateY(-50%);
}

